.footerBlock {
    color: white;
    background: #09090c;
    grid-area: footer;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px;

}

.links {
    display: flex;
    flex-direction: row;
    width: 300px;
    justify-content: space-between;

}

.border {
    border: 1px solid rgba(255, 255, 255, 0.73);
    width: 50%;
    margin: 20px;
}

/*===========================================SubFooter==========================================*/
.main {
    grid-area: main;
}

.second {
    grid-area: second;
}

.third {
    grid-area: third;
}

.fover {
    grid-area: fover;
}

.subFooter {
    margin: 0 auto;
    grid-area: subFooter;
    background-color: #1a1a1a;
    width: 100%;
    height: 150px;
    color: white;
    font-size: 24px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 20px;

}

.icons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon {
    margin-top: 10px;
    display: flex;
    position: relative;
    right: 35px;
}

.news {
    margin-bottom: 20px;
    text-align: center;

}

.iconVK {

    background-image: url('./vk.png');
